.menu {
    display: -webkit-flex;
    display: flex;
    width: 100vw;
    height: 100vh;
    position: fixed;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: all 0.2s;
  
}

.menu.active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}


.content{
    width: 50%;
    height: 100%;
    background-color: #151B1F;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    transition: all 0.4s;
    -webkit-justify-content: center;
            justify-content: center;
    padding-left:  128px;
}


ul li a{
    color: white;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 32px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

ul {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    list-style-type:none;
    height: 182px;
    text-decoration: none;

}

li a:hover{
    color: #0EC261;

}

.media{
    width: 144px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 44px;
}

.icons{
    fill:white;
}

.icons:hover{
    fill:  #0EC261;
}


.swiper-container {
    width: 45%;
    height: 100vh;

  }
  
  .swiper-slide {
    background-size:cover;
    width: 100%;
    height: 100%;
    position: relative;
  }

  .swiper-slide::after{
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
    mix-blend-mode: normal;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,.5);
    z-index: 2;
  }


.swiper-button-prev::after,
.swiper-button-next::after {
    color:  #EEEEEE;
    font-size: 18px;
    font-weight: 700;
    height: 100%;
    width: 100%;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.swiper-button-prev:hover:after,
.swiper-button-next:hover:after{
    background: rgba(14, 194, 97, 0.2);
}

.swiper-button-prev,
.swiper-button-next{
    width: 64px;
    height: 100%;
    top: 0;
    margin-top: 0;
   
}

.swiper-button-prev{
    left: 0;
}

.swiper-button-next{
    right: 0;
}

.swiper-pagination-bullet{
    opacity: 1;
    background-color: #EEEEEE;
}

.swiper-pagination-bullet-active{
    opacity: 1;
    background-color: #0EC261;
}

.inner{
